import React, { memo } from 'react'
import { IconProps } from '~svg'
import cls from 'classnames'

const RocketIcon: React.FC<IconProps> = props => {
  const { className, ...otherProps } = props

  return (
    <svg
      className={cls('fill-current', className)}
      viewBox="0 0 15 16"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      {...otherProps}
    >
      <path d="M0 15.05H15V0.0499999H0V15.05Z" fill="url(#pattern0)"/>
      <defs>
        <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
          <use xlinkHref="#image0" transform="scale(0.015625)"/>
        </pattern>
        <image id="image0" width="64" height="64" xlinkHref="data:image/png;base64,"/>
      </defs>
    </svg>
  )
}

export default memo(RocketIcon)