import React, { memo } from 'react'
import { IconProps } from '~svg'
import cls from 'classnames'

const ZoomIcon: React.FC<IconProps> = props => {
  const { className, ...otherProps } = props

  return (
    <svg 
      className={cls('fill-current', className)}
      viewBox="0 0 20 20" 
      fill="none" 
      xmlns="http://www.w3.org/2000/svg" 
      xmlnsXlink="http://www.w3.org/1999/xlink"
      {...otherProps}>
      <rect width="20" height="20" rx="4" fill="url(#pattern0)"/>
      <rect width="20" height="20" rx="4" fill="url(#pattern1)"/>
      <rect width="20" height="20" rx="4" fill="url(#pattern2)"/>
      <defs>
        <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
          <use xlinkHref="#image0" transform="scale(0.00434783)"/>
        </pattern>
        <pattern id="pattern1" patternContentUnits="objectBoundingBox" width="1" height="1">
          <use xlinkHref="#image1" transform="scale(0.00434783)"/>
        </pattern>
        <pattern id="pattern2" patternContentUnits="objectBoundingBox" width="1" height="1">
          <use xlinkHref="#image2" transform="scale(0.00434783)"/>
        </pattern>
        <image id="image0" width="230" height="230" xlinkHref="data:image/png;base64,"/>
        <image id="image1" width="230" height="230" xlinkHref="data:image/png;base64,"/>
        <image id="image2" width="230" height="230" xlinkHref="data:image/png;base64,"/>
      </defs>
    </svg>
    
  )
}

export default memo(ZoomIcon)